/**
 * this function listens  for a post message from the extension
 * when it gets that message, it sets a global flag in the
 * `window` that extension is installed
 * this get called because the extension injects a content-script to
 * our app that will post this message
 */
export function listenForExtensionInstallEvent() {
  window.addEventListener('message', (event: MessageEvent) => {
    // We only accept messages from ourselves
    if (event.source !== window) {
      return;
    }
    if (event.data === 'CFY-INSTALLED') {
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      (window as any).CFY_INSTALLED = true;
    }
  });
}

/**
 * fires extension is installed message
 */
export function triggerExtensionInstallEvent() {
  window.postMessage('CFY-INSTALLED', window.location.origin);
}

/**
 * checks if extension is installed, will only work if both
 * `listenForExtensionInstallEvent` and `triggerExtensionInstallEvent`
 * are called in the app
 */
export function isExtensionInstalled() {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  return (window as any).CFY_INSTALLED === true;
}
